let that;
import {
	nuGoodName,
	upGoodsImg
} from '@/axios/index.js'
import {
	generateUUID
} from '@/utils/util.js'
import videoPlay from '@/components/video-play.vue'
export default {
	components: {
		videoPlay
	},
	data() {
		return {
			video: '',
			show: false,
			imgList: [],
			isCanPrivate: 1, //1图片商品 2视频商品
			info: '', //商品详情
			goodsId: '', //商品id
			videoList:[]
		}
	},
	onLoad(options) {
		that = this;
		that.isCanPrivate = options.isCanPrivate
		console.log(options)
	},
	methods: {
		ChooseImage() { //选择素材
			if (that.isCanPrivate == 1) {

				uni.chooseImage({
					count: 8,
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['camera', 'album'], //从相册选择
					success: (res) => {
						if (that.imgList.length != 0) {
							that.imgList = that.imgList.concat(res.tempFilePaths)
						} else {
							that.imgList = res.tempFilePaths
						}

					}
				});
			} else {
				uni.chooseVideo({
					count: 1,
					sourceType: ['camera', 'album'],
					maxDuration: 15,
					success: function(res) {
						console.log(res)
						// if(res.duration > 15) {
						// 	uni.showToast({
						// 		title: `视频时长不能大于15秒`,
						// 		duration: 2000,
						// 		icon: 'none'
						// 	});
						// 	return false;
						// }
						that.videoList = [res.tempFilePath];
						console.log(that.videoList)
					}
				});
			}
		},
		ViewImage(i) { //图片预览
			uni.previewImage({
				urls: that.imgList,
				current: i
			});
		},
		showVideo: function() {  //视频预览
			that.video = that.videoList[0]
			that.show = true
		},
		hideVideo: function() { //关闭视频预览
			that.show = false
		},
		DelVideo: function() { //清除视频
			that.videoList = []
		},
		DelImg: function(index) {//清除图片
			that.imgList.splice(index,1)
		},
		upGoods: async function() { //保存
			var list = that.isCanPrivate ==1?that.imgList:that.videoList
			if (!list.length) {
				uni.showToast({
					title: `请上传${that.isCanPrivate==1?"图片":"视频"}`,
					duration: 2000,
					icon: 'none'
				});
				return false;
			}
			if (!that.info) {
				uni.showToast({
					title: `商品描述不能为空`,
					duration: 2000,
					icon: 'none'
				});
				return false;
			}
			var params = {
				name: `${generateUUID(32,16)}`,
				code: `${generateUUID(32,16)}@${new Date().getTime()}`,
				unit: that.info,
				isCanPrivate: that.isCanPrivate,
			}

			await nuGoodName(params).then(res => {
				uni.showLoading({
					title: '保存中...',
					mask: true,
				})
				if (res.ret == 0) {
					that.goodsId = res.info
				}
			})
			if(that.isCanPrivate == 1) { //保存图片商品
				
				var arr = []
				that.imgList.forEach((el, i) => {
					arr.push(that.upImg(el, i))
				})
				Promise.all(arr).then((res) => {
					uni.hideLoading()
					uni.$emit('getGoods')
					uni.showToast({
						title: `保存成功`,
						duration: 3000,
						icon: 'none',
						mask: true
					});
					that.$Router.replaceAll({name:'home'})
					
				}).catch(err => {
					console.log(err)
				});
			} else { //保存视频商品
				that.upImg(that.videoList[0]).then((res)=>{
					uni.hideLoading()
					uni.$emit('getGoods')
					uni.showToast({
						title: `保存成功`,
						duration: 2000,
						icon: 'none',
						mask: true
					});
					that.$Router.replaceAll({name:'home'})
				})
			}
		},
		upImg: function(url, seqno=1) { //上传

			return new Promise((resolve, reject) => {
				upGoodsImg(url, {
					goodsNameId: that.goodsId,
					seqno: seqno,
					type: 1
				}).then(res => {
					resolve(res)
				}).catch((err) => {
					reject(err)
				})
			})
		}
	}
}
